/* hover effects */
#rot-img:hover{
    -webkit-animation: rotate 3s alternate;
            animation: rotate 3s alternate;
}

.sympt:hover{
    -webkit-animation: scale 2s alternate;
            animation: scale 2s alternate;
}

#crowd:hover{
    -webkit-animation: scale 1.3s alternate infinite;
            animation: scale 1.3s alternate infinite;
}

#touch:hover{
    -webkit-animation: scale 1.3s alternate infinite;
            animation: scale 1.3s alternate infinite;
}

#cover:hover{
    -webkit-animation: scale 1.3s alternate infinite;
            animation: scale 1.3s alternate infinite; 
}

@-webkit-keyframes rotate {
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes scale {
    0%{
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    100%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}

@keyframes scale {
    0%{
    -webkit-transform: scale(1);
            transform: scale(1);
    }
    100%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}
